<template>
  <div>
    <nav class="navbar navbar-project-bar navbar-expand-md navbar-light">
      <div class="navbar-nav mr-auto ml-auto">
        <router-link
          v-for="menu in menus"
          :key="menu._id"
          active-class="active"
          class="nav-item nav-link"
          v-html="menu.title"
          :to="{
          name:menu.routeName,
          params:menu.params
          }"
          replace>
        </router-link>
        <router-link
          v-for="(appItem,index) in applications"
          :key="index"
          active-class="active"
          class="nav-item nav-link"
          v-html="appItem.title"
          :to="{name:appItem.routeName}"
          replace>
        </router-link>
      </div>
    </nav>
    <div class="project-app-view">
      <loading v-if="isLoading"></loading>
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
  import Api from '@/utils/api'
  import Auth from '@/utils/auth'

  import Loading from '../../components/Loading'

  export default {
    name: 'Project',
    components: {
      Loading
    },
    computed: {
      project: {
        get() {
          return this.$store.state.project;
        },
        set(project) {
          this.$store.dispatch('setProject', project);
        }
      }
    },
    created() {
      this.projectId = this.$route.params.project_id;

      //同步Project
      this.$store.dispatch('getProjectAsync', this.projectId);

      this.initTaskList();
      this.initTaskGroups();
    },
    data() {
      return {
        isLoading: true,
        taskGroups: [],
        projectId: null,
        menus: [],
        applications: [
          // {title: '需求', routeName: 'ProjectDemand'},
          // {title: '缺陷', routeName: 'ProjectDefect'},
          // {title: '迭代', routeName: 'ProjectIteration'},
          {title: '分享', routeName: 'ProjectGroupChat'},
          {title: '文件', routeName: 'ProjectGroupChat'},
          {title: '日程', routeName: 'ProjectGroupChat'},
          {title: '统计', routeName: 'ProjectGroupChat'},
          {title: '群聊', routeName: 'ProjectGroupChat'},
          {title: '成员', routeName: 'ProjectGroupChat'},
        ]
      }
    },
    methods: {
      //初始化Task列表
      initTaskList() {

        Api.taskList({
          project_id: this.projectId
        }).then((res) => {
          this.taskList = [];
          res.forEach((item) => {
            let menu = {
              _id: item._id,
              title: item.title,
              routeName: 'ProjectTask',
              params: {
                'project_id': item._project_id,
                'task_list_id': item._id
              }
            };
            console.log(menu);
            this.menus.push(menu)
            //this.menus.push();
            this.taskList.push(item);
            //在TaskList初始化完成后的阶段
            this.isLoading = false;
          });
        });
      },
      //获取Task阶段
      initTaskGroups() {
        Api.taskGroups({project_id: this.projectId}).then((res) => {
          this.taskGroups = [];
          res.forEach((group) => {
            this.taskGroups.push(group);
          })
        });
      },
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
  .navbar-project-bar {
    border-bottom: 1px solid #dddddd;
    padding: 0;
    .nav-item {
      &.nav-link {
        font-size: 15px;
        line-height: 35px;
        color: #000;
        margin: 0 .5em;
        &.active {
          border-bottom: 3px solid $color-primary;
        }
      }
    }
  }

  .project-app-view {
    background: #f5f5f5;
  }
</style>



